import React from "react";
import {Link, useLocation} from "react-router-dom";
import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    Dropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    Container,
    InputGroup,
    InputGroupText,
    InputGroupAddon,
    Input, NavLink,
} from "reactstrap";
import logo from "klogo-small.png";
import data from "data.json";

function Header(props) {
    const [isOpen, setIsOpen] = React.useState(false);
    const [dropdownOpen, setDropdownOpen] = React.useState(false);
    const [color, setColor] = React.useState("transparent");
    const sidebarToggle = React.useRef();
    const location = useLocation();
    const toggle = () => {
        if (isOpen) {
            setColor("transparent");
        } else {
            setColor("dark");
        }
        setIsOpen(!isOpen);
    };
    const dropdownToggle = (e) => {
        setDropdownOpen(!dropdownOpen);
    };
    // const getBrand = () => {
    //
    //   let brandName = "Home";
    //   // routes.map((prop, key) => {
    //   //   if (window.location.href.indexOf(prop.layout + prop.path) !== -1) {
    //   //     brandName = prop.name;
    //   //   }
    //   //   return null;
    //   // });
    //   return brandName;
    // };
    const listOfNotifications = () => {
        const list = [];
        for ( let i of data.notifications) {
            list.push( <DropdownItem tag="a">{i}</DropdownItem>);
        }
        console.log(list)
        return list;
    };
    const openSidebar = () => {
        document.documentElement.classList.toggle("nav-open");
        sidebarToggle.current.classList.toggle("toggled");
    };
    // function that adds color dark/transparent to the navbar on resize (this is for the collapse)
    const updateColor = () => {
        if (window.innerWidth < 993 && isOpen) {
            setColor("dark");
        } else {
            setColor("transparent");
        }
    };
    React.useEffect(() => {
        window.addEventListener("resize", updateColor.bind(this));
    });
    React.useEffect(() => {
        if (
            window.innerWidth < 993 &&
            document.documentElement.className.indexOf("nav-open") !== -1
        ) {
            document.documentElement.classList.toggle("nav-open");
            sidebarToggle.current.classList.toggle("toggled");
        }
    }, [location]);
    return (
        // add or remove classes depending if we are on full-screen-maps page or not
        <Navbar
            color={color}
            className={
                props.location.pathname.indexOf("full-screen-maps") !== -1
                    ? "navbar-absolute fixed-top"
                    : "navbar-absolute fixed-top " +
                    (color === "transparent" ? "navbar-transparent " : "")
            }
        >
            {/*<Container fluid>*/}
            {/*    <div className="navbar-wrapper">*/}

    <div className="logo">
        <a
            href="https://www.creative-tim.com"
            className="simple-text logo-mini"
        >
            <div className="logo-img">
                <img src={logo} alt="react-logo"/>
            </div>
        </a>
    </div>
                    <Nav tabs={true}>

                        <NavItem className="active">
                            <NavLink href="/components/">Home</NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink href="">Explorer</NavLink>
                        </NavItem>
                    </Nav>
                    <Nav>
                        <NavItem>
                            <form style={{width: "660px"}}>
                                <InputGroup className="no-border">
                                    <Input placeholder="Search..."/>
                                    <InputGroupAddon addonType="append">
                                        <InputGroupText>
                                            <i className="nc-icon nc-zoom-split"/>
                                        </InputGroupText>
                                    </InputGroupAddon>
                                </InputGroup>
                            </form>
                        </NavItem>

                    <NavItem>
                        <Link to="#pablo" className="nav-link btn-magnify">
                            <i className="nc-icon nc-chat-33"/>
                            <p>
                                <span className="d-lg-none d-md-block">Stats</span>
                            </p>
                        </Link>
                    </NavItem>
                    <Dropdown
                        nav
                        isOpen={dropdownOpen}
                        toggle={(e) => dropdownToggle(e)}
                    >
                        <DropdownToggle caret nav>
                            <i className="nc-icon nc-bell-55"/>
                            <p>
                                <span className="d-lg-none d-md-block">Some Actions</span>
                            </p>
                        </DropdownToggle>
                        <DropdownMenu right>
                            {listOfNotifications()}
                        </DropdownMenu>
                    </Dropdown>
                    <NavItem>
                        <Link to="#pablo" className="nav-link btn-rotate">
                            <div className="header-avatar avatar">
                                <img
                                    alt="..."
                                    className="img-circle img-no-padding img-responsive"
                                    src={
                                        require("assets/img/faces/ayo-ogunseinde-2.jpg")
                                            .default
                                    }
                                />
                            </div>
                        </Link>
                    </NavItem>
                </Nav>
            {/*</div>*/}
        {/*</Container>*/}
</Navbar>
)
    ;
}

export default Header;
